import React from 'react';
import { hot } from "react-hot-loader";

import Filter from '../../components/filter';
import Search from '../../components/search';
import Areas from '../../components/areas';
import MobileTab from '../../components/tab';

class Averages extends React.PureComponent {
    render() {
        const { currentTab } = this.state;

        return (
            <section>
                <MobileTab />
                <div className="container hidden-xs hidden-sm">
                    <Filter>
                        {(data, FilterFields) => (
                            <>
                                <FilterFields />
                                <Areas {...data} />
                                <Search {...data} />
                            </>
                        )}
                    </Filter>
                </div>
                <div className="container">
                    <Filter>
                        {(data, FilterFields) => (
                            <>
                                {currentTab === 0 && <Areas {...data} />}
                                {currentTab === 1 && <Search {...data} />}
                                {currentTab === 2 && <FilterFields />}
                            </>
                        )}
                    </Filter>
                </div>
            </section>
        )
    }
}

export default hot(module)(Averages);