import React from 'react';
import PropTypes from 'prop-types';

class Tab extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            currentTab: 0,
        };

        this.changeTab = this.changeTab.bind(this);
    }
    
    changeTab (tabIndex) {
        return () => this.setState({ currentTab: tabIndex });
    } 

    render() {
        return (
            <>
                <div className="mobile-nav-tab" >
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <div className="col-lg-4 col-md-4 col-sm-4 col-xs-4 mobile-tab" onClick={this.changeTab(0)}>List</div>
                        <div className="col-lg-4 col-md-4 col-sm-4 col-xs-4 mobile-tab" onClick={this.changeTab(1)}>Map</div>
                        <div className="col-lg-4 col-md-4 col-sm-4 col-xs-4 mobile-tab" onClick={this.changeTab(2)}>Filter</div>
                    </div>
                </div>
                {this.props.children[this.state.currentTab] || null}
            </>
        );
    }
}

Tab.propTypes = {
    children: PropTypes.children
}

export default Tab;